.container {
  width: 50%;
  padding: 0.2em;
  background-color: black;
  color: white;
}

.container.grid {
  width: 50%;
}

.container.list {
  width: 100%;
}
.video {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  border: 1px solid rgb(29, 29, 29);
  box-shadow: 3px 3px 5px 0px rgb(12, 12, 37);
  cursor: pointer;
  transition: transform 250ms ease-in;
}

.video:hover {
  border: 1px solid yellow;
}

.thumbnail {
  width: 40%;
  height: 100%;
}

.metadata {
  margin-left: 0.2em;
}

.title,
.channel {
  margin: 0;
  font-size: 0.8rem;
}

.channel {
  font-size: 0.6rem;
}
